import './App.css';
import React from 'react';
import axios from 'axios';

class App extends React.Component{
  constructor(){
    super();
    this.state = {
      name: "",
      password1: "",
      password2: "",
      email: "",
      message: "",
      errorMessage: "",
      thanks: false
    }
  }

  componentDidMount(){
    document.querySelector('#reg-username').addEventListener('keydown', (e) => {
      if (e.key === "Enter"){
        this.formSubmit();
      }
    })
    document.querySelector('#reg-pass1').addEventListener('keydown', (e) => {
      if (e.key === "Enter"){
        this.formSubmit();
      }
    })
    document.querySelector('#reg-pass2').addEventListener('keydown', (e) => {
      if (e.key === "Enter"){
        this.formSubmit();
      }
    })
    document.querySelector('#reg-email').addEventListener('keydown', (e) => {
      if (e.key === "Enter"){
        this.formSubmit();
      }
    })
  }

  textHandler = e => {
    this.setState({
      ...this.state,
      [e.target.name]: e.target.value
    })
  }

  checkIfEmailInString = (text) => { 
    var re = /(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))/;
    return re.test(text);
}

  formSubmit = () => {
    if (this.state.password1 === this.state.password2){
      if (this.checkIfEmailInString(this.state.email)){
        if (this.state.email.length < 129){
          if (this.state.name.length < 31){
            if (this.state.password1.length < 257){
              if (this.state.email.split('\\').length > 1){
                this.setState({
                  ...this.state,
                  errorMessage: "Invalid character '\\' in email address"
                })
              } else if (this.state.name.split('\\').length > 1){
                this.setState({
                  ...this.state,
                  errorMessage: "Invalid character '\\' in username"
                })
              } else if (this.state.password1.split('\\').length > 1){
                this.setState({
                  ...this.state,
                  errorMessage: "Invalid character '\\' in password"
                })
              } else {
                const fd = new FormData();
              fd.append("name", this.state.name);
              fd.append("password1", this.state.password1);
              fd.append("password2", this.state.password2);
              fd.append("email", this.state.email);
              this.setState({
                ...this.state,
                message: "Submitting...",
                errorMessage: ""
              });
              axios.post('https://nanaimg.net/register', fd)
                .then(res => {
                  if (res.data.success){
                    this.setState({
                      ...this.state,
                      thanks: true
                    })
                  } else if (res.data.fail){
                    this.setState({
                      ...this.state,
                      message: "",
                      errorMessage: res.data.fail
                    })
                  }
        })
        .catch(err => {
          this.setState({
            ...this.state,
            errorMessage: "An error occurred. Please try again.",
            message: ""
          })
        })
              }
            } else {
              this.setState({
                ...this.state,
                errorMessage: "Your password is too long (max: 256 characters)"
              })
            }
          } else {
            this.setState({
              ...this.state,
              errorMessage: "Your username is too long (max: 30 characters)"
            })
          }
        } else {
          this.setState({
            ...this.state,
            errorMessage: "Your email is too long."
          })
        }
      } else {
        this.setState({
          ...this.state,
          errorMessage: "Please enter a valid email"
        })
      }
    } else {
      this.setState({
        ...this.state,
        errorMessage: "Your passwords do not match."
      })
    }
    
  }

  render(){
    return (
      <>
      {(this.state.thanks === false) ? <div id="register-form">
        <h1 id="h1-must-register">Register</h1>
        <div className="register-field"><label className="labels-register-form">Username: </label> <input onChange={this.textHandler} name="name" type="text" className="input-register-form" placeholder="max: 30 characters" id="reg-username"/></div><br/>
        <div className="register-field"><label className="labels-register-form">Password: </label> <input onChange={this.textHandler} name="password1" type="password" className="input-register-form" id="reg-pass1"/></div><br/>
        <div className="register-field"><label className="labels-register-form">Re-enter Password: </label> <input onChange={this.textHandler} name="password2" type="password" className="input-register-form" id="reg-pass2"/></div><br/>
        <div className="register-field"><label className="labels-register-form">Email: </label> <input onChange={this.textHandler} name="email" type="email" className="input-register-form" id="reg-email"/></div>
        <div id="button-container-register">
          <button onClick={this.formSubmit} id="button-register">Register</button> 
        </div>
        {this.state.errorMessage === "" ? <p></p> : <p id="comment-error-message">{this.state.errorMessage}</p>}
        {this.state.message === "" ? <p></p> : <p id="comment-upload-message">{this.state.message}</p>}
      </div> : <h1 id="h1-register-thanks">Thank you for your submission. Your registration will be reviewed shortly.</h1>}
      
      </>
    )
  }
}

export default App;
